/* == UI COMPONENTS == */
/* buttons */
[type="button"],
[type="submit"],
button,
.a-button,
.yellow-button,
.transp-button,
.green-button {
  display: inline-block;
  text-decoration: none;
  font-size: 1rem;
  font-family: "Space Mono", monospace;
  padding: .6rem 1.2rem;
  vertical-align: middle;
  text-align: center;
  font-weight: 500;
  cursor: pointer;
  white-space: nowrap;
  line-height: 1;
  border-radius: 10px;
  /* max-width: 90%; */
  margin-bottom: 3%;
  margin-top: 10px;
}

[type="button"],
[type="submit"],
button,
.a-button {
  background: #b4d0fa3a;
  color: #3448ff;
  border: 2px solid #b4d0fa3a;
}

button:focus {
  background: rgb(227, 227, 227);
}



.transp-button {
  background: none;
  color: #444444;
}

.transp-button:hover {
  background-color: #ffffb1;
}


[type="button"]:hover,
[type="submit"]:hover,
button:hover {
  background-color: white;
}


/* table form */
div .table {
  display: table;
  border-collapse: collapse;
}

div .thead {
  display: table-header-group;
  color: #444444;
  padding: 1rem;
  border-bottom: 3px solid #d2d2d2;
  font-weight: 500;
}

div .tbody {
  display: table-row-group;
}

form .tr,
div .tr {
  display: table-row;
}

.td {
  display: table-cell;
  padding: 1rem;
  text-align: center;
  border: 2px solid #f0f0f0;
}

