/* body */
head,
body {
  text-align: justify;
  color: #444444;
  background-color: #fdfdfd;
  /* padding: 1.2rem; */
}

body {
  box-sizing: border-box;
}

/* Anchors */
a {
  border-bottom: 3px solid #beebaa44;
  color: #75a012;
  font-weight: 500;
  font-size: 1rem;
  text-decoration: none;
}

a:hover:not(.a-button) {
  background-color: #beebaa44;
}

::selection {
  background-color: #ffde75;
  color: #444444;
}

/* paragraph */
/* == HEADERS == */
h1,
h2,
h3,
h4 {
  color: #444444;
  line-height: 1.2;
}

h1 {
  font-size: 2.6rem;
}

h4 {
  padding: 10px;
}

:focus {
  outline: none;
}

/**/
.wrapper,
.container {
  max-width: 1200px;
  width: 100%;
  text-align: center;
  justify-content: center;
  padding-left: 10px;
  padding-right: 10px;
  margin: 0 0;
  display: flex;
  align-items: center;
  flex-direction: column;
  flex-wrap: wrap;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.container::before,
.container::after,
.wrapper::before,
.wrapper::after {
  display: table;
  content: "";
  clear: both;
  width: 100%;
}

.container {
  border: 1px solid #cecece;
  box-shadow: 2px 5px 5px rgba(0, 0, 0, 0.06);
}

/* == BREAKPOINTS == */
@media screen and (min-width: 650px) {

  .wrapper,
  .container {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    text-align: center;
  }

  h1,
  h2,
  h3,
  h4 {
    align-content: center;
    vertical-align: middle;
    width: 100%;
  }
}

/* grid */

.row {
  display: flex;
}

.individual {
  flex: 1 1;
  float: left;
  /* min-height: 100%; */
}

.row:after {
  content: '';
  display: table;
  clear: both;
}

ul {
  margin: 0;
  padding: .5rem;
  list-style: none;
  display: block;
  min-width: 200px;
  min-height: 100%;
  border: 1px solid saddlebrown;
  -webkit-padding-start: 0;
          padding-inline-start: 0;
}

li {
  margin: 10px;
}

@media screen and (max-width: 700px) {
  /* .column {
    width: 100%;
    text-align: justify;
  } */

  .column img {
    max-width: 320px;
    max-height: 400px;
  }
}

/* photo grid */
.grid-container {
  margin: 0 auto;
  padding: 0 1rem;
  max-width: 1200px;
}

.responsive-img {
  max-width: 100%;
  height: auto;
}


@media screen and (min-width: 400px) {
  .grid {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
  }


  .cell {
    width: calc(50% - 2rem);
    max-width: 100%;
    min-width: 200px;
    min-height: 2rem;
    text-align: center;
    border-top: 1px solid lightgray;
  }

}


/* site title */
.site-header {
  background-color: #ffffff;
  grid-column: 1 / 3;
  position: static;
  top: 10%;
  margin-top: 21px;
  z-index: 1000;
  padding-left: 2rem;
  border-bottom: 2px solid #d7d6d6;
}

.site-title {
  font-size: 1.5rem;
  color: black;
}

.text-headers {
  text-align: center;
}
.content-editable {
    padding: 1rem;
    min-width: 150px;
    /* max-width: 100%; */
    resize: none;
}

.content-editable:hover {
    background: #dddddd;
}

.content-editable:focus {
    background: rgb(215, 241, 255);
    outline: 0;
}

table {
    table-layout: fixed;
    max-width: 100%;
